import React from "react";
import styled from "styled-components";

const Attachments = ({ handleRemove, items }) => {
  return (
    <>
      {items.map((attachment, idx) => (
        <Attachment>
          {attachment.name}
          <CloseIcon
            onClick={() => handleRemove(idx)}
            className="fa fa-times"
          />
        </Attachment>
      ))}
    </>
  );
};

export default Attachments;

const Attachment = styled.span`
  font-size: 12px;
  line-height: 18px;
  background-color: white;
  border-radius: 4px;
  padding: 5px 8px;
  width: calc(50% - 12px);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding-right: 25px;
  position: relative;

  &:nth-child(even) {
    margin: 0 8px 4px 4px;
  }
  &:nth-child(odd) {
    margin: 0 4px 4px 8px;
  }
`;

const CloseIcon = styled.i`
  color: ${({ theme }) => theme.colors.primary};
  font-size: 12px;
  margin-left: auto;
  cursor: pointer;
  display: flex;
  align-self: center;
  position: absolute;
  right: 8px;
  top: 50%;
  transform: translateY(-50%);
`;
