import React from "react";
import styled from "styled-components";
import { theme } from "../../../../styles/theme";

const TextAreaInput = React.forwardRef(({
  name,
  label,
  onChange,
  onBlur,
  onKeyDown,
  placeholder,
  value,
  error,
  customStyles,
  disabled,
}, ref) => {
  let wrapperClass = "form-group";
  if (error && error.length > 0) {
    wrapperClass += " has-error";
  }

  return (
    <div className={wrapperClass}>
      {label && <StyledLabel htmlFor={name}>{label}</StyledLabel>}
      <div className="field">
        <StyledInput
          ref={ref}
          rows="2"
          name={name}
          className={`form-control ${error ? "is-invalid" : ""}`}
          placeholder={placeholder}
          value={value}
          onChange={onChange}
          onBlur={onBlur}
          onKeyDown={onKeyDown}
          style={customStyles}
          disabled={disabled}
        />
        {error && <ErrorMessage className="invalid-feedback">{error}</ErrorMessage>}
      </div>
    </div>
  );
});

const StyledInput = styled.textarea`
  background-color: ${({ theme }) => theme.colors.paleGrey};
  resize: none;
  padding: ${({ customStyles }) =>
    customStyles && customStyles.padding
      ? `${customStyles.padding}`
      : "0.375rem 2rem 0.375rem 0.75rem"};
  font-size: ${({ customStyles }) =>
    customStyles && customStyles.fontSize
      ? `${customStyles.fontSize}`
      : `16px`};
  line-height: 22px;
  font-weight: normal;
  && {
    height: ${({ customStyles }) =>
      customStyles && customStyles.height ? `${customStyles.height}` : `140px`};
  }
  &:focus {
    outline: none;
    border-color: #ced4da;
    box-shadow: none;
    background-color: ${theme.colors.paleGrey};
  }

  ::placeholder {
    color: ${({ theme }) => theme.colors.lightGrey};
  }
  ::-webkit-scrollbar {
    width: 14px;
    height: 14px;
    background-color: ${({ theme }) => theme.colors.paleGrey};
  }
  ::-webkit-scrollbar-thumb {
    height: 8px;
    border: 4px solid rgba(0, 0, 0, 0);
    background-clip: padding-box;
    -webkit-border-radius: 9px;
    background-color: ${({ theme }) => theme.colors.lightGrey};
  }
  ::-webkit-scrollbar-button {
    width: 0;
    height: 0;
    display: none;
  }
  ::-webkit-scrollbar-corner {
    background-color: transparent;
    background-color: ${({ theme }) => theme.colors.paleGrey};
  }
`;

const StyledLabel = styled.label`
  color: #929599;
  font-size: 16px;
  font-weight: 500;
`;

const ErrorMessage = styled.span`
  position: absolute;
`;

export default TextAreaInput;
