import React, { useEffect, useRef, useState } from 'react';
import styled, { css } from 'styled-components';

import { validate } from '../../utils/forms';
import Attachments from '../../../Attachments';
import AttachIcon from '../../../AttachIcon';
import TextAreaInput from '../../../TextArea';
import sendIcon from '../../../../../../assets/sendIcon.svg';
import { theme } from '../../../../../../styles/theme';

const initialChat = {
  reply: '',
};

const initialValidations = {
  isValid: false,
  reply: { valid: false, error: '', required: true },
};

const textAreaWrapperYPadding = 15;
const textAreaBorderHeight = 1;
const isAttachmentsWorking = false;

const ChatTextArea = React.forwardRef(
  (
    {
      chatStatus,
      handleChatIssueInit,
      handleSendChatMessage,
      isChatOver,
      isSubmitting,
    },
    textAreaRef
  ) => {
    const { chatConnecting, chatStarted } = chatStatus;
    const [message, setMessage] = useState(initialChat);
    const [attachments, setAttachments] = useState([]);
    const [textAreaHeight, setTextAreaHeight] = useState('initial');
    const [formValidation, setFormValidation] = useState(initialValidations);
    const textAreaWrapperRef = useRef(null);
    const textAreaAttachmentsRef = useRef(null);
    const attachmentsLength = attachments.length;

    useEffect(() => {
      if (isChatOver && attachmentsLength) {
        setAttachments([]);
      }
    }, [isChatOver]);

    useEffect(() => {
      const currentTextAreaHeight =
        textAreaRef.current.clientHeight + textAreaBorderHeight;
      const currenTextAreaAttachmentsHeight =
        textAreaAttachmentsRef.current.clientHeight + textAreaBorderHeight;
      const newTextAreaWrapperHeight =
        textAreaWrapperYPadding * 2 +
        currentTextAreaHeight +
        currenTextAreaAttachmentsHeight;

      setTextAreaHeight(`${newTextAreaWrapperHeight}px`);
    }, [
      attachments.length,
      textAreaWrapperRef.current && textAreaWrapperRef.current.clientHeight,
    ]);

    const clear = () => {
      setMessage(initialChat);
      setFormValidation(initialValidations);
    };

    const handleChange = (e) => {
      const messagePayload = {
        ...message,
        reply: e.target.value,
      };
      setMessage(messagePayload);
    };

    const handleSendMessage = (e) => {
      if (!e.key || e.key === 'Enter') {
        e.preventDefault();

        const validated = validate(message, formValidation, 'reply');
        setFormValidation(validated);

        if (!validated.isValid) {
          return;
        }

        return chatStarted
          ? handleSendChatMessage(message, clear)
          : handleChatIssueInit(e, message, clear);
      }
    };

    const handleRemoveAttachment = (idx) => {
      const attachmentsUpdate = [
        ...attachments.slice(0, idx),
        ...attachments.slice(idx + 1),
      ];
      setAttachments(attachmentsUpdate);
      const messageUpdate = {
        ...message,
        attachments: attachmentsUpdate,
      };
      setMessage(messageUpdate);
    };

    const handleAddAttachmentsOnChange = (e) => {
      e.preventDefault();
      const attachmentsUpdate = [...attachments, ...e.target.files];
      setAttachments(attachmentsUpdate);
      const messageUpdate = {
        ...message,
        attachments: attachmentsUpdate,
      };
      setMessage(messageUpdate);
    };

    return (
      <TextAreaWrapper dynamicHeight={textAreaHeight} ref={textAreaWrapperRef}>
        <TextAreaInput
          ref={textAreaRef}
          placeholder="Type your issue here"
          value={isChatOver ? '' : message.reply}
          onChange={handleChange}
          onKeyDown={handleSendMessage}
          name="reply"
          customStyles={{
            height: '55px',
            fontSize: '14px',
            padding: "'0.375rem 0.75rem'",
            borderBottomLeftRadius: 'unset',
            borderBottomRightRadius: 'unset',
            borderBottom: 'none',
          }}
          disabled={chatConnecting || isSubmitting || isChatOver}
        />
        <TextAreaIconsContainer
          isEven={attachmentsLength % 2 === 0}
          isSubmitting={chatConnecting || isSubmitting || isChatOver}
          ref={textAreaAttachmentsRef}
        >
          {!!attachmentsLength && (
            <Attachments
              handleRemove={handleRemoveAttachment}
              items={attachments}
            />
          )}
          {isAttachmentsWorking && (
            <AttachIcon
              disabled={isSubmitting || isChatOver}
              handleAdd={handleAddAttachmentsOnChange}
            />
          )}
          <SendIconWrapper
            disabled={chatConnecting || isSubmitting || isChatOver}
            onClick={handleSendMessage}
          >
            <SendIcon src={sendIcon} />
          </SendIconWrapper>
        </TextAreaIconsContainer>
      </TextAreaWrapper>
    );
  }
);

export default ChatTextArea;

const TextAreaWrapper = styled.div`
  display: flex;
  flex-direction: column;
  min-height: ${({ dynamicHeight }) => dynamicHeight};
  position: relative;
  display: ${(props) => (props.defaultView ? 'flex' : 'block')};
  padding: ${textAreaWrapperYPadding}px 33px;
  transition: all 100ms ease-in-out;
`;

const TextAreaIconsContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  position: relative;
  width: 100%;
  min-height: 33px;
  border-bottom-left-radius: 0.25rem;
  border-bottom-right-radius: 0.25rem;
  background-color: ${({ isSubmitting }) =>
    isSubmitting ? '#e9ecef' : theme.colors.paleGrey};
  border: ${textAreaBorderHeight}px solid #ced4da;
  border-top: none;
  margin-top: -1rem;
  padding-bottom: ${({ isEven }) => (isEven ? '36px' : '4px')};
`;

const Icon = css`
  cursor: pointer;
  width: 16px;
  height: 16px;
`;

const SendIconWrapper = styled.div`
  display: flex;
  align-items: center;
  cursor: pointer;
  position: absolute;
  bottom: 8px;
  right: 8px;
  width: 28px;
  height: 28px;
  padding: 6px;
  background-color: ${({ disabled }) =>
    disabled ? '#e9ecef' : theme.colors.white};
  border-radius: 4px;
  &:hover {
    background-color: ${({ disabled }) =>
      disabled ? '#e9ecef' : theme.colors.highlightGrey};
  }
`;

const SendIcon = styled.img`
  ${Icon}
`;
