import React, { useState } from "react";
import styled from "styled-components";
import Tab from "./Tab";

const TabComponents = props => {
  const [currentTabIdx, setCurrentTabIdx] = useState(0);

  return (
    <>
      <StyledHeader>
        <StyledTabsWrapper>
          {props.items.map((item, idx) => (
            <Tab
              key={`tab-${item.text}_${idx}`}
              text={item.text}
              isActive={idx === currentTabIdx}
              handleClick={() => setCurrentTabIdx(idx)}
            />
          ))}
        </StyledTabsWrapper>
        <CloseIcon onClick={props.handleClick} className="fa fa-times" />
      </StyledHeader>

      {props.items.map(({ Component, text }, idx) => (
        <StyledComponentWrapper
          key={`component-${text}_${idx}`}
          isActive={currentTabIdx === idx}
        >
          <Component />
        </StyledComponentWrapper>
      ))}
    </>
  );
};

export default TabComponents;

const StyledHeader = styled.div`
  display: flex;
  justify-content: space-between;
  margin-bottom: 1.75rem;
  padding: 33px 33px 0 33px;
`;

const CloseIcon = styled.i`
  color: ${({ theme }) => theme.colors.primary};
  font-size: 1.75rem;
  margin-left: auto;
  cursor: pointer;
  display: flex;
  align-self: center;
`;

const StyledTabsWrapper = styled.div`
  display: flex;
`;

const StyledComponentWrapper = styled.div`
  display: ${({ isActive }) => (isActive ? "block" : "none")};
`;
