import React, { useEffect, useRef } from "react";
import moment from "moment";
import striptags from "striptags";
import styled from "styled-components";
import ReactTooltip from "react-tooltip";
import ChatAgentAvatar from "../ChatAgentAvatar";
import MessageDate from "../ChatMessageDate";
import { theme } from "../../../../../../styles/theme";

const ChatMessages = ({ chatStatus }) => {
  const lastMessageRef = useRef(null);

  useEffect(() => {
    lastMessageRef &&
      lastMessageRef.current &&
      lastMessageRef.current.scrollIntoView();
  }, [chatStatus.messages.length]);

  const setMessageDate = (date) => {
    const momentDate = moment(date);
    const today = moment(new Date()).startOf("day");
    const yesterday = moment(new Date()).subtract(1, "days").startOf("day");

    if (momentDate.isSame(today, "day")) {
      return momentDate.format("LT");
    } else if (momentDate.isSame(yesterday, "day")) {
      return momentDate.subtract(1, "days").calendar(null, {
        lastDay: "[Yesterday] LT",
      });
    } else {
      const diffInDays = today.diff(momentDate, "days");
      return momentDate.subtract(diffInDays, "days").calendar(null, {
        sameElse: "MM/DD LT",
      });
    }
  };

  const parseContent = (content) => {
    const noHTMLContent = striptags(content);
    const noSpecialCharsContent = noHTMLContent.replace(/&nbsp;/g, "");

    return noSpecialCharsContent;
  };

  const groupedMessages = chatStatus.messages.reduce(
    (messagesArr, currMessage) => {
      if (!messagesArr.length) {
        messagesArr.push([currMessage]);
        return messagesArr;
      }

      const messagesArrCurrentPosition = messagesArr.length - 1;

      if (
        currMessage.is_user ===
        messagesArr[messagesArrCurrentPosition][0].is_user
      ) {
        messagesArr[messagesArrCurrentPosition].push(currMessage);
      } else {
        messagesArr.push([currMessage]);
      }

      return messagesArr;
    },
    []
  );
  const groupedMessagesLength = groupedMessages.length - 1;

  return (
    <>
      {groupedMessages.map((group, currGroupedMessagesIdx) => {
        const groupLength = group.length - 1;

        return (
          <MessagesGroupContainer key={currGroupedMessagesIdx}>
            {group.map((message, idx) => {
              const isLastChatMessage =
                groupedMessagesLength === currGroupedMessagesIdx &&
                groupLength === idx;
              const messageTooltipDate = setMessageDate(message.date_created);

              if (message.is_sys) {
                return null;
              }

              if (message.is_user) {
                return (
                  <MessageUserWrapper key={message.id}>
                    <MessageUser
                      data-tip={messageTooltipDate}
                      onMouseEnter={ReactTooltip.rebuild}
                    >
                      <MessageText>{message.content}</MessageText>
                    </MessageUser>
                    {isLastChatMessage && (
                      <MessageDate owner="user" date={message.date_created} />
                    )}
                  </MessageUserWrapper>
                );
              }

              return (
                <MessageAgentWrapper key={message.id}>
                  <MessageAgentContainer>
                    {idx === groupLength && (
                      <ChatAgentAvatar
                        avatar={chatStatus.agent.avatar}
                        height="32"
                        inMessage
                        width="32"
                      />
                    )}
                    <MessageAgent
                      isLastMessage={idx === groupLength}
                      data-tip={messageTooltipDate}
                      onMouseEnter={ReactTooltip.rebuild}
                    >
                      <MessageText>{parseContent(message.content)}</MessageText>
                    </MessageAgent>
                  </MessageAgentContainer>
                  {isLastChatMessage && (
                    <MessageDate owner="agent" date={message.date_created} />
                  )}
                </MessageAgentWrapper>
              );
            })}

            <ReactTooltip
              className="react-tooltip-opacity"
              arrowColor="transparent"
              place="bottom"
              backgroundColor={theme.colors.darkGrey}
            />

            <div ref={lastMessageRef} />
          </MessagesGroupContainer>
        );
      })}
    </>
  );
};

const chatMessagesAreEqual = (prevProps, nextProps) =>
  prevProps.chatStatus.messages.length === nextProps.chatStatus.messages.length;

export default React.memo(ChatMessages, chatMessagesAreEqual);

const MessagesGroupContainer = styled.div`
  margin-bottom: 16px;
  display: flex;
  flex-direction: column;
`;

const MessageUserWrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-self: flex-end;
  width: 64%;
`;

const MessageUser = styled.div`
  width: 100%;
  background-color: #336fb9;
  color: ${theme.colors.white};
  display: flex;
  align-self: flex-end;
  justify-content: flex-start;
  margin-bottom: 8px;
  border-radius: 8px;
  font-weight: normal;
  font-size: 14px;
  line-height: 21px;
  padding: 0.6rem;
`;

const MessageAgentWrapper = styled.div`
  width: 64%;
`;

const MessageAgentContainer = styled.div`
  display: flex;
  align-items: flex-end;
`;

const MessageAgent = styled.div`
  background-color: ${theme.colors.paleBlue};
  width: 100%;
  margin-bottom: 8px;
  color: ${theme.colors.black};
  border-radius: 8px;
  font-weight: normal;
  font-size: 14px;
  line-height: 21px;
  padding: 0.6rem;
  margin-left: ${({ isLastMessage }) => (isLastMessage ? "0;" : "42px;")};
`;

const MessageText = styled.span`
  word-break: break-word;
`;
