import React, { useState } from "react";
import styled from "styled-components";
import ChatSupportIcon from "../ChatSupportIcon";
import ThumbsUp from "../ChatFeedbackIcons/ThumbsUp";
import ThumbsDown from "../ChatFeedbackIcons/ThumbsDown";
import { theme } from "../../../../../../styles/theme";

const isFeedbackWorking = false;

const ChatFeedback = ({ agentName, sendChatFeedback }) => {
  const [selectedIdx, setSelectedIdx] = useState(null);

  const handleRatingClick = (idx, value) => {
    if (selectedIdx === null) {
      setSelectedIdx(idx);
      sendChatFeedback(value);
    }
  };

  if (!isFeedbackWorking) {
    return (
      <>
        <FeedbackText>
          <ChatSupportIcon inChat />
          <span>
            The chat session has ended.
          </span>
        </FeedbackText>
      </>
    );
  }

  return (
    <>
      <FeedbackText>
        <ChatSupportIcon inChat />
        <span>
          The chat session has ended. Would you like to leave feedback for{" "}
          {agentName}? You can choose a star rating and/or post a comment.
        </span>
      </FeedbackText>

      <RaitingWrapper>
        <FeedbackButton
          isSelected={selectedIdx === 0}
          onClick={() => handleRatingClick(0, true)}
        >
          <ThumbsUp />
          <FeedbackButtonText>This was helpful!</FeedbackButtonText>
        </FeedbackButton>
        <FeedbackButton
          isSelected={selectedIdx === 1}
          onClick={() => handleRatingClick(1, false)}
        >
          <ThumbsDown />
          <FeedbackButtonText>Not helpful</FeedbackButtonText>
        </FeedbackButton>
      </RaitingWrapper>

      {selectedIdx !== null && (
        <FeedbackText>
          <ChatSupportIcon inChat />
          <span>
            Thank you for your feedback. We are constantly evaluating the quality of our support department to be of better service to you.
          </span>
        </FeedbackText>
      )}
    </>
  );
};

export default ChatFeedback;

const FeedbackText = styled.div`
  display: flex;
  align-items: flex-end;
  width: 64%;
  font-weight: normal;
  font-size: 14px;
  line-height: 21px;
  color: ${theme.colors.black};
  margin-bottom: 1rem;
`;

const RaitingWrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-bottom: 1.25rem;
`;

const FeedbackButton = styled.div`
  background-color: ${({ isSelected }) =>
    isSelected ? "#336fb9" : theme.colors.paleGrey};
  border-radius: 4px;
  color: ${({ isSelected }) =>
    isSelected ? theme.colors.white : theme.colors.primary};
  padding: 8px 10px;
  display: flex;
  align-items: flex-end;
  text-align: center;
  cursor: ${({ isSelected }) =>
    isSelected ? "default" : "cursor"};
  &:hover {
    background-color: ${({ isSelected }) =>
      isSelected ? "#336fb9" : theme.colors.highlightGrey};
  }
  &:nth-child(1) {
    margin-right: 12px;
  }
`;

const FeedbackButtonText = styled.span`
  font-size: 10px;
  font-weight: normal;
  line-height: 15px;
  margin-left: 7px;
`;

