import React from 'react';
import styled, { keyframes } from "styled-components";

import AgentAvatar from '../ChatAgentAvatar';
import SupportIcon from '../ChatSupportIcon';
import { theme } from "../../../../../../styles/theme";

const ChatHeader = ({
  chatStatus,
  handleEndChat,
  handleInitChat,
  isChatOver,
  isEndChatAllowed,
}) => {
  const { agent, chatConnecting, chatSession, chatStarted } = chatStatus;

  return (
    <StyledChatHeader>
      {!chatStarted ? (
        <ChatHeaderIconContainer>
          {chatConnecting ? (
            <>
              <SupportIcon />
              <ChatHeaderIconConnecting>
                Connecting
                <ConnectingDot>.</ConnectingDot>
                <ConnectingDot>.</ConnectingDot>
                <ConnectingDot>.</ConnectingDot>
              </ChatHeaderIconConnecting>
            </>
          ) : (
            <div onClick={handleInitChat}>
              <SupportIcon />
              <ClickableText>Connect with live chat</ClickableText>
            </div>
          )}
        </ChatHeaderIconContainer>
      ) : (
        <AgentContainer>
          <AgentInfo>
            <AgentAvatar avatar={chatStatus.agent.avatar} />
            <AgentInfoText>
              <AgentName>{agent.name}</AgentName>
              <AgentTitle> {chatSession.department_name}</AgentTitle>
            </AgentInfoText>
          </AgentInfo>

          {!isChatOver ? (
            <div onClick={isEndChatAllowed ? handleEndChat : undefined}>
              <ClickableText disabled={!isEndChatAllowed}>
                End Chat
              </ClickableText>
            </div>
          ) : (
            <StaticText>Chat Ended</StaticText>
          )}
        </AgentContainer>
      )}
    </StyledChatHeader>
  );
};

export default ChatHeader;

const StyledChatHeader = styled.div`
  display: flex;
  flex-direction: column;
  padding: 0 33px;
  height: 50px;
`;

const ChatHeaderIconContainer = styled.div`
  display: flex;
  align-items: center;
`;

const ChatHeaderIconConnecting = styled.span`
  color: ${theme.colors.darkGrey};
`;

const ClickableText = styled.span`
  font-weight: 500;
  font-size: 16px;
  line-height: 23px;
  color: ${({ disabled }) =>
    disabled ? theme.colors.paleGrey : theme.colors.primary};
  cursor: ${({ disabled }) => (disabled ? "default" : "pointer")};
`;

const connectingDotAnimation = keyframes`
  0% {
    opacity: .2;
  }
  20% {
    opacity: 1;
  }
  100% {
    opacity: .2;
  }
`;

const ConnectingDot = styled.span`
  animation-name: ${connectingDotAnimation};
  animation-duration: 1.4s;
  animation-iteration-count: infinite;
  animation-fill-mode: both;

  &:nth-child(2) {
    animation-delay: 0.2s;
  }
  &:nth-child(3) {
    animation-delay: 0.4s;
  }
`;

const AgentContainer = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
`;

const AgentInfo = styled.div`
  display: flex;
`;

const AgentInfoText = styled.div`
  display: flex;
  flex-direction: column;
`;

const AgentName = styled.h4`
  font-weight: 500;
  font-size: 16px;
  line-height: 23px;
  color: ${theme.typo.colors.black};
  margin-bottom: 0;
`;

const AgentTitle = styled.span`
  font-weight: normal;
  font-size: 12px;
  line-height: 12px;
  color: ${theme.colors.darkGrey};
`;

const StaticText = styled.span`
  font-weight: 500;
  font-size: 16px;
  line-height: 23px;
  color: ${theme.colors.grey};
`;