import React, { useRef } from 'react';
import styled from 'styled-components';
import attachIcon from "../../../../assets/attachIcon.svg";
import { theme } from "../../../../styles/theme";

const AttachIcon = ({ disabled, handleAdd, right }) => {
  const attachmentInputRef = useRef(null);

  const handleAddAttachmentsTrigger = () =>
    attachmentInputRef.current.click();

  const handleAddAttachmentsOnClick = (e) => (e.target.value = null);

  return (
    <AttachIconWrapper
      disabled={disabled}
      onClick={handleAddAttachmentsTrigger}
      right={right}
    >
      <Icon src={attachIcon} />
      <HiddenInput
        multiple
        onChange={handleAdd}
        onClick={handleAddAttachmentsOnClick}
        ref={attachmentInputRef}
        type="file"
      />
    </AttachIconWrapper>
  );
}

export default AttachIcon;

const AttachIconWrapper = styled.div`
  display: flex;
  align-items: center;
  cursor: pointer;
  position: absolute;
  bottom: 8px;
  right: ${({ right }) => right ? right : '40px'};
  width: 28px;
  height: 28px;
  padding: 6px;
  background-color: ${({ disabled }) =>
    disabled ? "#e9ecef" : theme.colors.white};
  border-radius: 4px;
  &:hover {
    background-color: ${({ disabled }) =>
      disabled ? "#e9ecef" : theme.colors.highlightGrey};
  }
`;

const Icon = styled.img`
  cursor: pointer;
  width: 16px;
  height: 16px;
`;

const HiddenInput = styled.input`
  visibility: hidden;
  opacity: 0;
  height: 0;
  width: 0;
`;