import React, { useContext, useState } from 'react';
import styled, { css } from 'styled-components';
import TextArea from './EmailTextArea';
import TextInput from '../../../../TextInput/TextInput';
import PrimaryButton from '../../../../PrimaryButton';
import { createTicket } from '../../../deskpro/services';
import { validate } from '../utils/forms';
import successIcon from './../../assets/CheckIconLight.svg';
import { HelpUserContext } from '../../..';

const initialForm = {
  subject: '',
  problem: '',
};

const initialValidations = {
  isValid: false,
  subject: { valid: false, error: '', required: true, minLength: 10 },
  problem: { valid: false, error: '', required: true, minLength: 10 },
};

const Email = () => {
  const [formValues, setFormValues] = useState(initialForm);
  const [formValidation, setFormValidation] = useState(initialValidations);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitSuccess, setSubmitSuccess] = useState(false);
  const [files, setFiles] = useState([]);

  const userContext = useContext(HelpUserContext);
  const clear = () => {
    setFormValues(initialForm);
    setFiles([]);
    setFormValidation(initialValidations);
  };

  const handleChange = (e) => {
    if (e.target.name === 'attachments') {
      setFiles(e.target.value);
      return;
    }
    const updatedFormValues = {
      ...formValues,
      [e.target.name]: e.target.value,
    };

    setFormValues(updatedFormValues);

    const validated = validate(
      updatedFormValues,
      formValidation,
      e.target.name
    );

    if (validated.isValid) {
      /* when validation is valid onChange let the override to clear residual errors */
      setFormValidation({
        ...validated,
      });
    } else {
      /* Only check the valid value and set that one to avoid triggering errors
       onChange and also let the submit button be available as soon as its valid */
      setFormValidation({
        ...formValidation,
        isValid: validated.isValid,
      });
    }
  };

  const handleBlur = (e) => {
    const validated = validate(formValues, formValidation, e.target.name);

    setFormValidation({
      ...validated,
    });
  };

  const submitTicket = async (e, values) => {
    e.preventDefault();
    const validated = validate(values, formValidation);

    setIsSubmitting(true);
    setFormValidation(validated);

    if (validated.isValid) {
      const ticketPayload = {
        userId: userContext.id,
        userName: userContext.userName,
        userEmail: userContext.email,
        subject: `${values.subject}`,
        message: `${values.problem}`,
      };
      if (files.length) {
        ticketPayload.attachments = files;
      }
      const result = await createTicket(ticketPayload);

      if (result.success) {
        setSubmitSuccess(true);
        setTimeout(() => {
          clear();
          setSubmitSuccess(false);
        }, 5000);
      }
      setIsSubmitting(false);
    }
  };
  return (
    <EmailContainer>
      {!submitSuccess && (
        <>
          <StylesTitle>Describe your issue and we're on it.</StylesTitle>
          <TextInput
            label="Subject"
            placeholder="Enter the reason for contacting us"
            value={formValues.subject}
            onChange={handleChange}
            onBlur={handleBlur}
            name="subject"
            error={formValidation.subject.error}
            disabled={isSubmitting}
          />

          <TextArea
            disabled={isSubmitting}
            error={formValidation.problem.error}
            handleOnBlur={handleBlur}
            handleOnChange={handleChange}
            name="problem"
            value={formValues.problem}
          />

          <PrimaryButtonWrapper>
            <PrimaryButton
              disabled={!formValidation.isValid || isSubmitting}
              onClick={(e) => submitTicket(e, formValues)}
              type="Submit"
            >
              Submit
            </PrimaryButton>
          </PrimaryButtonWrapper>
        </>
      )}

      {submitSuccess && (
        <ResultContainer>
          <ResultIconWrap className="icon">
            <ResultIcon src={successIcon} />
          </ResultIconWrap>
          <ResultTitle>Success</ResultTitle>
          <ResultContent>
            {' '}
            Thanks for connecting! We will follow up within one business day.
          </ResultContent>
        </ResultContainer>
      )}
    </EmailContainer>
  );
};

export default Email;

const Icon = css`
  cursor: pointer;
  width: 16px;
  height: 16px;
`;

const ResultContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  padding-top: 22%;
`;
const ResultTitle = styled.span`
  width: 106px;
  height: 32px;
  font-family: Heebo;
  font-style: normal;
  font-weight: 500;
  font-size: 26px;
  line-height: 38px;
  display: flex;
  align-items: center;
  color: #000000;
  margin-top: 3%;
`;
const ResultContent = styled.span`
  width: 260px;
  height: 48px;
  font-family: Heebo;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 23px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #303133;
  margin-top: 3%;
`;
const ResultIcon = styled.img`
  width: 70px;
  height: 70px;
`;
const ResultIconWrap = styled.div`
  display: flex;
  align-items: center;
`;
const EmailContainer = styled.div`
  padding: 0px 33px 33px 33px;
`;

const StylesTitle = styled.h3`
  font-size: 16px;
  font-weight: 300;
  margin-bottom: 15px;
`;

const PrimaryButtonWrapper = styled.div`
  display: flex;
  justify-content: center;
`;
