import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import SlidingPane from 'react-sliding-pane';
import Modal from 'react-modal';
import Tabs from './ui/Tabs';
import { Chat, Email } from './ui/HelpWidgets';

// Styles
import 'react-sliding-pane/dist/react-sliding-pane.css';
import './index.css';

const tabs = [
  {
    Component: Email,
    key: 'email',
    text: 'Email',
  },
  {
    Component: Chat,
    key: 'chat',
    text: 'Chat',
  },
];

export const HelpDrawerContext = React.createContext({});
export const HelpUserContext = React.createContext({});

const HelpDrawer = (props) => {
  const [isDrawerOpen, setIsDrawerOpen] = useState(props.isOpen);

  const drawerStore = {
    isOpen: { get: isDrawerOpen, set: setIsDrawerOpen },
  };

  useEffect(() => {
    setIsDrawerOpen(props.isOpen);
  }, [props.isOpen]);

  useEffect(() => {
    Modal.setAppElement(document.getElementById('root'));
  }, []);

  return (
    <HelpUserContext.Provider value={props.user}>
      <HelpDrawerContext.Provider value={drawerStore}>
        <SlidingPane
          className="custom-sliding-pane"
          overlayClassName="custom-overlay-class"
          isOpen={isDrawerOpen}
        >
          <Tabs
            items={tabs}
            handleClick={() => {
              drawerStore.isOpen.set(false);
              props.handleClose();
            }}
          />
        </SlidingPane>
      </HelpDrawerContext.Provider>
    </HelpUserContext.Provider>
  );
};

HelpDrawer.propTypes = {
  user: PropTypes.shape({
    id: PropTypes.string,
    email: PropTypes.string,
    userName: PropTypes.string,
  }),
};
export default HelpDrawer;
