import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { noAvatar } from '../../config/tempBaseAvatar';

function Avatar({ url, backupUrl, width = 32, height = 32, ...props }) {
  return (
    <AvatarWrap width={width} height={height} {...props}>
      <AvatarImage url={url} backupUrl={backupUrl || noAvatar} />
    </AvatarWrap>
  );
}

Avatar.propTypes = {
  url: PropTypes.string,
  backupUrl: PropTypes.string,
};

const AvatarWrap = styled.div`
  display: block;
  height: ${({ height }) => height}px;
  width: ${({ width }) => width}px;
  min-height: ${({ height }) => height}px;
  min-width: ${({ width }) => width}px;
  border: 1.5px solid #ffffff;
  border-radius: 50%;
  overflow: hidden;
`;

const AvatarImage = styled.div`
  height: 100%;
  width: 100%;
  background: url('${({ url, backupUrl }) => (url ? url : backupUrl)}');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
`;

export default Avatar;
