import React, { useRef, useState } from 'react';
import styled from 'styled-components';

import Attachments from '../../../Attachments';
import AttachIcon from '../../../AttachIcon';
import TextAreaInput from '../../../TextArea';
import { theme } from '../../../../../../styles/theme';

const EmailTextArea = ({
  disabled,
  error,
  handleOnBlur,
  handleOnChange,
  name,
  value,
}) => {
  const [attachments, setAttachments] = useState([]);
  const textAreaAttachmentsRef = useRef(null);
  const attachmentsLength = attachments.length;

  const handleRemoveAttachment = (idx) => {
    const attachmentsUpdate = [
      ...attachments.slice(0, idx),
      ...attachments.slice(idx + 1),
    ];
    setAttachments(attachmentsUpdate);
    handleOnChange({
      target: {
        name: 'attachments',
        value: attachmentsUpdate,
      },
    });
  };

  const handleAddAttachmentsOnChange = (e) => {
    e.preventDefault();
    const attachmentsUpdate = [...attachments, ...e.target.files];
    setAttachments(attachmentsUpdate);
    handleOnChange({
      target: {
        name: 'attachments',
        value: attachmentsUpdate,
      },
    });
  };

  return (
    <TextAreaWrapper>
      <TextAreaInput
        customStyles={{
          height: '83px',
          fontSize: '14px',
          padding: "'0.375rem 0.75rem'",
          borderBottomLeftRadius: 'unset',
          borderBottomRightRadius: 'unset',
          borderBottom: 'none',
        }}
        disabled={disabled}
        error={error}
        label="Message"
        name={name}
        onBlur={handleOnBlur}
        onChange={handleOnChange}
        placeholder="Describe the problem in full detail"
        value={value}
      />
      <TextAreaIconsContainer
        isEven={attachmentsLength % 2 === 0}
        disabled={disabled}
        error={error}
        ref={textAreaAttachmentsRef}
      >
        {!!attachmentsLength && (
          <Attachments
            handleRemove={handleRemoveAttachment}
            items={attachments}
          />
        )}
        <AttachIcon
          disabled={disabled}
          handleAdd={handleAddAttachmentsOnChange}
          right="8px"
        />
      </TextAreaIconsContainer>
    </TextAreaWrapper>
  );
};

export default EmailTextArea;

const TextAreaWrapper = styled.div`
  display: flex;
  flex-direction: column;
  min-height: 100px;
  position: relative;
  display: ${(props) => (props.defaultView ? 'flex' : 'block')};
  transition: all 100ms ease-in-out;
  margin-bottom: 32px;
  & textarea {
    outline: none;
  }
  & textarea:focus {
    box-shadow: none !important;
  }
`;

const TextAreaIconsContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  position: relative;
  width: 100%;
  min-height: 33px;
  border-bottom-left-radius: 0.25rem;
  border-bottom-right-radius: 0.25rem;
  background-color: ${({ disabled }) =>
    disabled ? '#e9ecef' : theme.colors.paleGrey};
  border: ${({ error }) =>
    `1px solid ${error ? theme.colors.error : '#ced4da'}`};
  border-top: none;
  margin-top: -1rem;
  padding-bottom: ${({ isEven }) => (isEven ? '36px' : '4px')};
  transition: border 200ms ease-in-out;
`;
