import React from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import INFO from '../../assets/info_blue.svg';

function TextInput({
    name = '',
    label = '',
    disabled,
    onChange,
    id = null,
    onFocus,
    onBlur,
    placeholder,
    value,
    error,
    onKeyDown = null,
    autoFocus = false,
    type = 'text',
    style,
    inputRef,
    className = null,
    iconClassName = null,
    iconClick = () => {},
    iconSrc = null,
    info,
    infoIconSet = false,
    infoIconSrc = null,
    infoIconClicked = () => {},
    styleInput = null,
}) {
    let wrapperClass = `form-group ${className}`;
    if (error && error.length > 0) {
        wrapperClass += ' has-error';
    }

    return (
        <div style={style} className={wrapperClass}>
            <div style={{ display: 'flex', flexDirection: 'row', alignItems: 'center' }}>
                {label && <StyledLabel htmlFor={name}>{label}</StyledLabel>}
                {info && (
                    <InfoIcon src={infoIconSet ? infoIconSrc : INFO} onClick={infoIconClicked} />
                )}
            </div>
            <div style={{ position: 'relative' }} className="field">
                <StyledInput
                    style={styleInput}
                    onFocus={onFocus}
                    type={type}
                    ref={inputRef}
                    onBlur={onBlur}
                    id={id}
                    iconClassName={!!iconClassName}
                    iconSrc={!!iconSrc}
                    name={name}
                    onKeyDown={onKeyDown}
                    disabled={disabled}
                    autoFocus={autoFocus}
                    className={`form-control ${error ? 'is-invalid' : ''}`}
                    placeholder={placeholder}
                    value={value}
                    onChange={onChange}
                />
                {iconClassName && <Icon className={`fa ${iconClassName}`} onClick={iconClick} />}
                {iconSrc && <IconImage src={iconSrc} />}
                {error && <div className="invalid-feedback">{error}</div>}
            </div>
        </div>
    );
}

TextInput.propTypes = {
    name: PropTypes.string,
    label: PropTypes.string,
    placeholder: PropTypes.string,
    disabled: PropTypes.bool,
    value: PropTypes.string,
    error: PropTypes.string,
};

const InfoIcon = styled.img`
    margin-left: 8px;
    max-width: 16px;
    max-height: 16px;
    cursor: pointer;
    margin-bottom: 0.5rem;
`;

const StyledLabel = styled.label`
    color: #929599;
    font-size: 16px;
    font-weight: 500;
`;

const StyledInput = styled.input`
    background-color: ${({ theme }) => theme.colors.paleGrey};
    &::placeholder {
        color: ${({ theme }) => theme.colors.lightGrey};
    }
    background-image: none !important;
    padding: ${({ iconClassName, iconSrc }) => {
        if (iconClassName) {
            return '12px 12px 12px 46px';
        } else if (iconSrc) {
            return '12px 12px 12px 46px';
        }

        return '12px';
    }};
`;

const IconStyle = css`
    position: absolute;
    top: 12px;
    left: 16px;
    font-size: 16px;
    color: ${({ theme }) => theme.colors.grey};
`;
const Icon = styled.i`
    ${IconStyle}
`;

const IconImage = styled.img`
    ${IconStyle}
    top: 8px;
`;
export default TextInput;
