import React, { useEffect, useState } from 'react';
import moment from "moment";
import styled from "styled-components";
import { theme } from "../../../../../../styles/theme";

const ChatMessageDate = ({ owner, date }) => {
  const [formattedDate, setFormattedDate] = useState(moment(date).fromNow());

  useEffect(() => {
    const intervalId = setInterval(() => {
      setFormattedDate(moment(date).fromNow());
    }, 60000);

    return () => clearInterval(intervalId);
  }, []);

  return <MessageDate owner={owner}>{formattedDate}</MessageDate>;
};

export default ChatMessageDate;

const MessageDate = styled.span`
  display: flex;
  justify-content: ${({ owner }) =>
    owner === "agent" ? "flex-end" : "flex-start"};
  font-size: 12px;
  line-height: 18px;
  color: ${theme.colors.grey};
`;