import React from 'react';
import styled from 'styled-components';
import { theme } from '../../../../../styles/theme'

const Tab = (props) => (
  <StyledTab isActive={props.isActive} onClick={props.handleClick}>
    <StyledText isActive={props.isActive}>{props.text}</StyledText>
  </StyledTab>
);

export default Tab;

const StyledTab = styled.div`
  background-color: ${({ isActive }) =>
    isActive ? "#336fb9" : theme.colors.paleGrey};
  margin-right: 20px;
  padding: 7px 20px;
  border-radius: 4px;
  cursor: pointer;
`;

const StyledText = styled.div`
  color: ${({ isActive }) =>
    isActive ? theme.colors.white : theme.colors.primary};
`;