import React from "react";
import styled from "styled-components";

import Avatar from "../../../../../Avatar/Avatar";

const ChatAgentAvatar = ({
  avatar,
  height = "36",
  inMessage = false,
  width = "36",
}) => {
  const parseAgentAvatar = (avatar) => {
    let parsedAvatar = null;

    if (avatar) {
      let cloneInfo = { ...avatar };

      if (cloneInfo.url_pattern) {
        parsedAvatar = cloneInfo.url_pattern.replace("{{IMG_SIZE}}", 200);
      } else {
        parsedAvatar = cloneInfo.default_url_pattern.replace(
          "{{IMG_SIZE}}",
          200
        );
      }
      parsedAvatar = unescape(parsedAvatar);
    }

    return { avatar: parsedAvatar };
  };

  const agentAvatar = parseAgentAvatar(avatar);

  return (
    <AgentAvatar
      url={agentAvatar.avatar}
      width={width}
      height={height}
      inMessage={inMessage}
    />
  );
};

const AgentAvatarIsEqual = ({ avatar: prevAvatar }, { avatar: nextAvatar }) =>
  prevAvatar.url_pattern === nextAvatar.url_pattern &&
  prevAvatar.default_url_pattern === nextAvatar.default_url_pattern;

export default React.memo(ChatAgentAvatar, AgentAvatarIsEqual);

const AgentAvatar = styled(Avatar)`
  margin-right: 10px;
  margin-bottom: ${({ inMessage }) => (inMessage ? "8px" : "initial")};
`;
