import axios from 'axios';

const {
  REACT_APP_DESKPRO_BASE_URL: baseUrl,
  REACT_APP_DESKPRO_API_KEY: apiKey,
} = process.env;

const deskProAxiosV2 = axios.create({
  baseURL: `https://${baseUrl}/api/v2/`,
  headers: { Accept: 'application/json', Authorization: `key ${apiKey}` },
});

const COMMON_HEADERS = {
  content: {
    multipart: { 'Content-Type': 'multipart/form-data' },
    json: { 'Content-Type': 'application/json' },
  },
};

const handleDeskProError = (_error) => {
  if (_error.constructor.name === 'Error') {
    if (
      _error.response &&
      _error.response.data &&
      _error.response.data.errors
    ) {
      if (
        _error.response.data.errors.errors &&
        _error.response.data.errors.errors[0]
      ) {
        alert(_error.response.data.errors.errors[0].message);
        console.error(_error.response.data.errors.errors[0].message);
      }
      if (_error.response.data.errors.fields) {
        if (
          _error.response.data.errors.fields.message &&
          typeof _error.response.data.errors.fields.message === 'string'
        ) {
          alert(_error.response.data.errors.fields.message);
          console.error(_error.response.data.errors.fields.message);
        }
      }
    } else {
      alert(_error);
      console.error(_error);
    }
  }
  if (typeof _error === 'string') {
    alert(_error);
  }
};

export const createTicket = async ({
  userId,
  userName,
  userEmail,
  subject,
  message,
  attachments,
}) => {
  try {
    const SUPPORT_DEPARTMENT = process.env.REACT_APP_DESKPRO_DEPARTMENT;
    const { data: departmentsListing } = await getAvailableTicketDepartments();
    const department = departmentsListing.data.find(
      (d) => d.title === SUPPORT_DEPARTMENT
    );
    if (!department) {
      throw new Error(
        `DeskPro department with title:${SUPPORT_DEPARTMENT} not found`
      );
    }
    if (!department.is_tickets_enabled) {
      throw new Error(
        `DeskPro department with title:${SUPPORT_DEPARTMENT} is not enabled for tickets`
      );
    }
    const ticketPayload = {
      person: {
        name: userName,
        email: userEmail,
      },
      department: String(department.id),
      subject: subject,
    };
    let createdBlobs = null;
    if (attachments && attachments.length) {
      const blobActions = attachments.map((_att) => {
        const blobData = new FormData();
        blobData.append('file', _att, _att.name);
        return deskProAxiosV2.post(`blobs/temp`, blobData, {
          headers: COMMON_HEADERS.content.multipart,
        });
      });
      const uploadedBlobs = await Promise.all(blobActions);
      createdBlobs = uploadedBlobs.map((ub) => ub.data.data);
    }
    const ticketResponse = await deskProAxiosV2
      .post('tickets', ticketPayload, {
        headers: COMMON_HEADERS.content.json,
      })
      .catch((createTicketError) => {
        throw createTicketError;
      });
    const addTicketMessage = {
      message: message,
      format: 'text',
      is_note: false,
      person: {
        name: userName,
        email: userEmail,
      },
    };
    if (createdBlobs && createdBlobs.length) {
      createdBlobs.map((cb) => {
        addTicketMessage.attachments = {
          ...addTicketMessage.attachments,
          [cb.blob_id]: { blob_auth: cb.blob_auth },
        };
      });
    }

    const addTicketMessageResponse = await deskProAxiosV2
      .post(
        `tickets/${ticketResponse.data.data.id}/messages`,
        addTicketMessage,
        {
          headers: COMMON_HEADERS.content.json,
        }
      )
      .catch((putTicketError) => {
        throw putTicketError;
      });
    if (ticketResponse && addTicketMessageResponse) {
      return { success: true, extraInfo: '' };
    }
  } catch (error) {
    handleDeskProError(error);
    return { success: false, extraInfo: '' };
  }
};
export const getAvailableTicketDepartments = () => {
  return deskProAxiosV2.get('ticket_departments?page=1&count=200&limit=200');
};
export const getAvailableChatDepartments = () => {
  return deskProAxiosV2.get('chat_departments?page=1&count=200&limit=200');
};

export const getAvailableAgentsForChat = () => {
  return deskProAxiosV2.get(
    'agents?page=1&count=20&limit=20&online=1&online_for_chat=1'
  );
};
export const getUserChat = async ({ chatId }) => {
  try {
    const { data: chatResponse } = await deskProAxiosV2.get(
      `user_chats/${chatId}`
    );

    if (chatResponse.data) {
      const res = chatResponse.data;
      return { success: true, chat: res };
    }
  } catch (error) {
    handleDeskProError(error);
    return { success: false };
  }
};
