export const createUserChat = async ({
  userId,
  userName,
  userEmail,
  subject,
}) => {
  try {
    const SUPPORT_DEPARTMENT = process.env.REACT_APP_DESKPRO_DEPARTMENT;
    const { data: departmentsListing } = await getAvailableChatDepartments();
    const department = departmentsListing.data.find(
      (d) => d.title === SUPPORT_DEPARTMENT
    );
    if (!department) {
      throw new Error(
        `DeskPro department with title:${SUPPORT_DEPARTMENT} not found`
      );
    }
    if (!department.is_chat_enabled) {
      throw new Error(
        `DeskPro department with title:${SUPPORT_DEPARTMENT} is not enabled for chats`
      );
    }
    const { data: availableAgentsListing } = await getAvailableAgentsForChat();
    if (!availableAgentsListing.data.length) {
      throw new Error(
        `DeskPro no agents available for chat , please try later`
      );
    }
    const chatAgent = availableAgentsListing.data[0]; // choose first one for now but can be randomized
    if (!chatAgent) {
      throw new Error(`DeskPro chat agent not found`);
    }
    if (!chatAgent.online || !chatAgent.online_for_chat) {
      throw new Error(`DeskPro chat agent online status invalid`);
    }
    const chatPayload = {
      person: {
        name: userName,
        email: userEmail,
      },
      subject,
      agent: {
        id: chatAgent.id,
        name: chatAgent.name,
        email: chatAgent.primary_email,
      },
      email_validated: 0,
      chat_department: department.id,
    };
    const { data: chatCreation } = await deskProAxiosV2
      .post('user_chats', chatPayload, {
        headers: COMMON_HEADERS.content.json,
      })
      .catch((createChatError) => {
        throw createChatError;
      });
    if (chatCreation.data) {
      const res = chatCreation.data;
      return { success: true, chat: res, agent: chatAgent };
    }
  } catch (error) {
    handleDeskProError(error);
    return { success: false };
  }
};

export const createChatConversationMessage = async ({
  userId,
  userName,
  userEmail,
  is_user = true,
  content,
  conversationId,
  attachments = [],
}) => {
  try {
    if (!conversationId) {
      throw new Error(`You need a conversationId to post a message to a chat`);
    }
    const chatMessagePayload = {
      content: content,
      author: { email: userEmail, name: userName },
      is_user: is_user,
    };

    let createdBlob = null;
    if (attachments.length) {
      const blobData = new FormData();
      const attachment = attachments[0]; // FIXME: try one for now
      blobData.append('file', attachment, attachment.name);
      const { data: blobResult } = await deskProAxiosV2.post(
        `blobs/temp`,
        blobData,
        {
          headers: COMMON_HEADERS.content.multipart,
        }
      );
      createdBlob = blobResult.data;
    }
    if (createdBlob) {
      /*chatMessagePayload.attachments = {
        [createdBlob.blob_id]: { blob_auth: createdBlob.blob_auth },
      };*/
    }
    const { data: messageCreation } = await deskProAxiosV2
      .post(`user_chats/${conversationId}/messages`, chatMessagePayload, {
        headers: COMMON_HEADERS.content.json,
      })
      .catch((createMessageError) => {
        throw createMessageError;
      });
    if (messageCreation.data) {
      if (
        messageCreation.data.metadata &&
        messageCreation.data.metadata.is_sys
      ) {
        if (messageCreation.data.metadata.user_timed_out) {
          throw new Error('Chat timed out !!!');
        }
        if (messageCreation.data.metadata['0']) {
          throw new Error(messageCreation.data.metadata['0']);
        }
      }
      const res = messageCreation.data;
      return { success: true, message: res };
    }
  } catch (error) {
    handleDeskProError(error);
    return { success: false };
  }
};

export const getChatMessages = async ({ conversationId }) => {
  try {
    const { data: messagesResponse } = await deskProAxiosV2.get(
      `user_chats/${conversationId}/messages?page=1&count=200&limit=200`
    );

    if (messagesResponse.data) {
      return { success: true, messages: messagesResponse.data.reverse() };
    }
  } catch (error) {
    handleDeskProError(error);
    return { success: false };
  }
};

export const endChatSession = async ({ chatId }) => {
  try {
    const endResponse = await deskProAxiosV2.put(`user_chats/${chatId}/end`);
    return { success: endResponse.status === 204 };
  } catch (error) {
    handleDeskProError(error);
    return { success: false };
  }
};

export const getChatInProgress = async (localProgress) => {
  try {
    const { success, chat } = await getUserChat({ chatId: localProgress.chat });
    if (!chat) {
      throw new Error(`DeskPro chat progressed not found, start a new one`);
    }
    if (success) {
      const { success, messages } = await getChatMessages({
        conversationId: chat.id,
      });
      if (success) {
        const { success, department } = await getChatDepartmentById({
          departmentId: localProgress.department,
        });
        if (!department) {
          throw new Error(`DeskPro department not found`);
        }
        if (!department.is_chat_enabled) {
          throw new Error(`DeskPro department is not enabled for chats`);
        }
        if (success) {
          const { success, agent } = await getAgentById({
            agentId: localProgress.agent,
          });
          if (!agent) {
            throw new Error(`DeskPro chat agent not found`);
          }
          if (!agent.online || !agent.online_for_chat) {
            throw new Error(`DeskPro agent is not available anymore try later`);
          }
          if (success) {
            return { success: true, chat, messages, agent, department };
          }
        }
      }
    }
  } catch (error) {
    handleDeskProError(error);
    return { success: false };
  }
};
