import React, { useState, useEffect, useRef, useContext } from 'react';
import styled from 'styled-components';
import AgentAvatar from './ChatAgentAvatar';
import Header from './ChatHeader';
import Feedback from './ChatFeedback';
import TextArea from './ChatTextArea';
import Messages from './ChatMessages';
import {
  createUserChat,
  createChatConversationMessage,
  getChatMessages,
  endChatSession,
  getUserChat,
  getChatInProgress,
} from '../../../deskpro/services';
import usePolling from '../hooks/usePolling';
import useInterval from '../hooks/useInterval';
import { theme } from '../../../../../styles/theme';
import { HelpDrawerContext, HelpUserContext } from '../../..';

const defaultChatStatus = {
  started: false,
  ended: false,
  connecting: false,
  rating: 0, // rating for when chat is ended
  messages: [],
  chatSession: { conversation_id: null },
  agent: null,
  refreshInterval: 5000,
};

const LOCAL_PROGRESS = 'help-chat-progress';

const Chat = () => {
  const [chatStatus, setChatStatus] = useState(defaultChatStatus);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [isEndChatAllowed, setIsEndChatAllowed] = useState(false);
  const [isChatOver, setIsChatOver] = useState(false);
  const [isPolling, startPolling, stopPolling] = usePolling({
    asyncOperation: () =>
      getChatMessages({
        conversationId: chatStatus.chatSession
          ? chatStatus.chatSession.conversation_id
          : null,
      }),
    interval: 1000, // in milliseconds(ms)
    retryCount: 3, // this is optional
    onSuccess: (result) => {
      if (result && result.success && result.messages.length) {
        if (result.messages.length > chatStatus.messages.length) {
          setChatStatus({
            ...chatStatus,
            messages: result.messages,
          });
          return true;
        }
      }
      return false;
    },
    onFailure: () => console.log('handle failure'), // this is optional
  });
  const endOfChatRef = useRef(null);
  const textAreaRef = useRef(null);
  const context = useContext(HelpDrawerContext);
  const userContext = useContext(HelpUserContext);

  const [isCleared, clearInterval] = useInterval(() => {
    if (chatStatus.started && chatStatus.chatSession) {
      startPolling();
    }
  }, 1000);

  const currentChatStatus = useRef();
  currentChatStatus.current = chatStatus;

  const saveChatProgress = () => {
    if (currentChatStatus.current.started && !currentChatStatus.current.ended) {
      const chatProgress = {
        agent: currentChatStatus.current.chatSession.agent_id,
        department: currentChatStatus.current.chatSession.department_id,
        conversation: currentChatStatus.current.chatSession.conversation_id,
        chat: currentChatStatus.current.chatSession.id,
      };
      localStorage.setItem(LOCAL_PROGRESS, JSON.stringify(chatProgress));
    }
  };

  useEffect(() => {
    return () => {
      stopPolling();
      clearInterval();
    };
  }, []);

  useEffect(() => {
    const previousProgress = localStorage.getItem(LOCAL_PROGRESS);
    const getAsyncChat = async (progress) => {
      const refreshChatInfo = await getChatInProgress(progress);

      if (refreshChatInfo && refreshChatInfo.success) {
        const chatRefresh = {
          chatSession: refreshChatInfo.chat,
          messages: refreshChatInfo.messages,
          agent: refreshChatInfo.agent,
        };
        if (refreshChatInfo.chat.status === 'open') {
          setIsEndChatAllowed(true);
          setChatStatus({
            ...chatStatus,
            ...chatRefresh,
            started: true,
          });
        } else if (refreshChatInfo.chat.status === 'ended') {
          localStorage.removeItem(LOCAL_PROGRESS);
          setIsChatOver(true);
          setChatStatus({
            ...chatStatus,
            ...chatRefresh,
            ended: true,
          });
        }
      }
    };
    if (previousProgress) {
      getAsyncChat(JSON.parse(previousProgress));
    }
  }, []);

  useEffect(() => {
    if (chatStatus.started && chatStatus.chatSession && !chatStatus.ended) {
      startPolling();
    }
    return () => {
      if (isPolling) {
        stopPolling();
      }
    };
  }, [chatStatus.started, chatStatus.messages]);

  useEffect(() => {
    setTimeout(() => {
      if (textAreaRef && textAreaRef.current) {
        textAreaRef.current.focus();
      }
    }, 100);

    if (
      chatStatus.messages.length &&
      chatStatus.messages[chatStatus.messages.length - 1].is_sys
    ) {
      setIsChatOver(true);
    }
  }, [chatStatus.messages.length]);

  useEffect(() => {
    if (isChatOver) {
      endOfChatRef &&
        endOfChatRef.current &&
        endOfChatRef.current.scrollIntoView({ behaviour: 'smooth' });
    }
  }, [isChatOver]);

  const initChatSession = async (e, message, clear) => {
    e.preventDefault();
    setIsSubmitting(true);
    setChatStatus({
      ...chatStatus,
      connecting: true,
    });

    const result = await createUserChat({
      userId: userContext.id,
      userName: userContext.userName,
      userEmail: userContext.email,
      subject: `Support Chat Request`,
    });

    if (result && result.success) {
      const newChatStatus = {
        ...chatStatus,
        chatSession: result.chat,
        agent: result.agent,
        started: true,
        connecting: false,
      };
      setChatStatus(newChatStatus);
      saveChatProgress();
      if (message) {
        clear();
        sendChatMessage(message, clear, newChatStatus);
      }

      setTimeout(() => {
        setIsEndChatAllowed(true);
      }, 5000);
    } else {
      setChatStatus({
        ...chatStatus,
        connecting: false,
      });
    }

    setIsSubmitting(false);
  };

  const addChatMessage = (message, initialChatStatus) => {
    const currentMessages = [...chatStatus.messages, message];

    setChatStatus({
      ...chatStatus,
      ...initialChatStatus,
      messages: currentMessages,
    });
  };

  const sendChatMessage = async (message, clear, initialChatStatus) => {
    setIsSubmitting(true);

    const {
      chatSession: { conversation_id, person, person_name, person_email },
    } = initialChatStatus || chatStatus;

    const chatMessagePayload = {
      content: message.reply,
      conversationId: conversation_id,
      userEmail: userContext.email,
      userId: userContext.id,
      userName: userContext.userName,
    };
    if (message.attachments) {
      chatMessagePayload.attachments = message.attachments;
    }
    const result = await createChatConversationMessage(chatMessagePayload);

    if (result && result.success) {
      addChatMessage(result.message, initialChatStatus);
      clear();
    }

    setIsSubmitting(false);
  };

  const endChat = async () => {
    setIsSubmitting(true);
    setIsChatOver(true);

    const payload = {
      chatId: chatStatus.chatSession.id,
    };
    const result = await endChatSession(payload);

    if (result && result.success) {
      stopPolling();
      clearInterval();
      const refreshChatInfo = await getUserChat(payload);
      if (refreshChatInfo && refreshChatInfo.success) {
        if (refreshChatInfo.chat.status === 'ended') {
          localStorage.removeItem(LOCAL_PROGRESS);
          setChatStatus({
            ...chatStatus,
            ended: true,
          });
        }
      }
    }

    setIsSubmitting(false);
  };

  const sendChatFeedback = async (value) => {
    // TODO: requires deskpro support for implementation
    setChatStatus({
      ...chatStatus,
      rating: value,
    });
  };

  const chatConnecting = chatStatus.connecting;
  const chatStarted = chatStatus.started && chatStatus.agent && !chatConnecting;

  return (
    <Container>
      <Header
        chatStatus={{ ...chatStatus, chatConnecting, chatStarted }}
        handleInitChat={initChatSession}
        handleEndChat={endChat}
        isChatOver={isChatOver}
        isEndChatAllowed={isEndChatAllowed}
      />

      <MessagesContainer>
        {!chatStarted || !chatStatus.chatSession.conversation_id ? (
          <MessageConnectText>
            {!chatConnecting && (
              <span>
                Click "Connect with live chat" or type your message below
              </span>
            )}
          </MessageConnectText>
        ) : (
          <>
            <MessageIntro>
              <AgentAvatar
                avatar={chatStatus.agent.avatar}
                width="32"
                height="32"
              />{' '}
              <MessageIntroAgentText>
                You are now connected with {chatStatus.agent.name} in our
                support department.
              </MessageIntroAgentText>
            </MessageIntro>

            <Messages chatStatus={chatStatus} />

            {isChatOver && (
              <>
                <Feedback
                  agentName={chatStatus.agent.name}
                  sendChatFeedback={sendChatFeedback}
                />

                <WrapperClickableText onClick={() => context.isOpen.set(false)}>
                  Close Sidebar
                </WrapperClickableText>

                <ChatBottom ref={endOfChatRef} />
              </>
            )}
          </>
        )}
      </MessagesContainer>

      <TextArea
        chatStatus={{ chatConnecting, chatStarted }}
        isChatOver={isChatOver}
        isSubmitting={isSubmitting}
        ref={textAreaRef}
        handleSendChatMessage={sendChatMessage}
        handleChatIssueInit={initChatSession}
      />
    </Container>
  );
};

export default Chat;

const appHeaderHeight = 80;
const helpTabsHeight = 38;
const helpTabsPadding = 33;
const helpTabsMarging = 28;
const helpTabsTotalHeight = helpTabsHeight + helpTabsPadding + helpTabsMarging;

const Container = styled.div`
  min-height: 100%;
  display: flex;
  flex-direction: column;
  height: ${'calc(100vh - ' + (appHeaderHeight + helpTabsTotalHeight) + 'px)'};
`;

const MessagesContainer = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow: scroll;
  border-top: 1px solid ${theme.colors.lightGrey};
  border-bottom: 1px solid ${theme.colors.lightGrey};
  padding: 29px 33px 20px 33px;
  ::-webkit-scrollbar {
    width: 0px;
    background: transparent;
  }
  ::-webkit-scrollbar-thumb {
    background: transparent;
  }
`;

const MessageConnectText = styled.div`
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 500;
  font-size: 18px;
  line-height: 26px;
  width: 280px;
  text-align: center;
  margin: 0 auto;
  color: ${theme.colors.black};
`;

const MessageIntro = styled.div`
  display: flex;
  width: 85%;
  margin-bottom: 14px;
`;

const MessageIntroAgentText = styled.span`
  font-weight: normal;
  font-size: 14px;
  line-height: 21px;
  color: ${theme.colors.black};
`;

const WrapperClickableText = styled.span`
  display: flex;
  justify-content: flex-end;
  font-weight: 500;
  font-size: 16px;
  line-height: 23px;
  color: ${({ disabled }) =>
    disabled ? theme.colors.paleGrey : theme.colors.primary};
  cursor: ${({ disabled }) => (disabled ? 'default' : 'pointer')};
`;

const ChatBottom = styled.div`
  height: 1px;
`;
