export const validate = (values, validationReference, singleKey = null) => {
  // this is only expecting string
  let validationResponse = { ...validationReference };
  const hasPendingErrors = () => {};
  const addErrorData = (key, errorTag) => {
    validationResponse[key] = {
      ...validationResponse[key],
      valid: false,
      error: errorTag,
    };
    validationResponse.isValid = false;
  };
  const clearErrorData = (key) => {
    validationResponse[key] = {
      ...validationResponse[key],
      valid: true,
      error: '',
    };
    let hasPendingErrors = false;
    Object.values(validationResponse).map((vr) => {
      if (vr.constructor.name === 'Object' && vr.hasOwnProperty('valid')) {
        if (!vr.valid) {
          hasPendingErrors = true;
          return;
        }
      }
    });

    if (!hasPendingErrors) {
      //if there are pending errors then it should not set as valid
      validationResponse.isValid = true;
    }
  };
  const runValidations = (key, value) => {
    let errorQueued = false; // report just the first error found
    //if there are errors pending then avoid revalidate todo
    if (validationReference[key].required) {
      if (!value || value === '') {
        addErrorData(key, 'required');
        errorQueued = true;
      } else if (value && value !== '') {
        clearErrorData(key);
      }
    }
    if (validationReference[key].minLength && !errorQueued) {
      if (value.length < validationReference[key].minLength) {
        addErrorData(
          key,
          `Too short min of ${validationReference[key].minLength}`
        );
        errorQueued = true;
      } else if (value.length >= validationReference[key].minLength) {
        clearErrorData(key);
      }
    }
  };
  if (!singleKey) {
    Object.entries(values).map((_val) => {
      const key = _val[0];
      const value = _val[1];
      if (!validationReference[key])
        throw new Error(`Add ${key} to your validations`);
      runValidations(key, value);
    });
  } else {
    if (singleKey.constructor.name !== 'String')
      throw new Error(`Key needs to be a string`);
    if (!validationReference[singleKey])
      throw new Error(`Add ${singleKey} to your validations`);
    if (!values[singleKey] && values[singleKey] !== '')
      throw new Error(`Key ${singleKey} not found in values`);
    runValidations(singleKey, values[singleKey]);
  }

  return validationResponse;
};
