import React from 'react';
import styled, { css } from "styled-components";

import supportIcon from "../../../assets/Support.svg";

const ChatSupportIcon = ({ inChat }) => (
  <SupportIcon src={supportIcon} inChat={inChat} />
);

const Icon = css`
  cursor: pointer;
  width: 16px;
  height: 16px;
`;

const SupportIcon = styled.img`
  ${Icon}
  width: 30px;
  height: 30px;
  margin-right: ${({ inChat }) => (inChat ? "17px;" : "5px;")};
`;

export default ChatSupportIcon;