import React, { useState } from 'react';
import { ThemeProvider } from 'styled-components';
import Help from './components/Help/index.js';
import theme from './styles/theme';
import GlobalStyle from './styles/global-styles';
import './App.css';
import uuid from 'uuid/v4';
require('dotenv').config();

function App() {
  const forceUser = {
    id: uuid(),
    email: 'testhelp@example.com',
    userName: 'Test User',
  };
  const [isHelpOpen, setHelpIsOpen] = useState(false);
  return (
    <>
      <ThemeProvider theme={theme}>
        <div className="App">
          <header className="App-header">Header</header>
          <div
            style={{ marginTop: '1rem' }}
            onClick={(e) => {
              setHelpIsOpen(true);
            }}
          >
            Click
          </div>
          {/* User information should be provided to this component*/}
          <Help
            user={forceUser}
            isOpen={isHelpOpen}
            handleClose={() => setHelpIsOpen(false)}
          />
        </div>
      </ThemeProvider>
      <GlobalStyle />
    </>
  );
}

export default App;
