import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

const PrimaryButton = styled.button`
    color: #fff;
    padding-left: 24px;
    padding-right: 24px;
    padding-top:8px;
    padding-bottom: 8px;
    background-color: ${({ theme, disabled }) => {
      return disabled ? theme.colors.grey : theme.colors.primary;
    }};
    border-radius: 5px;
    border: none;
    transition: ${props => props.theme.animations.defaultTime}
    &:hover {
       background-color: ${({ theme, disabled }) => {
         return disabled ? theme.colors.grey : theme.colors.primary05;
       }};
    }
    &:focus {
        outline:0;
    }
`;

export default function index({ onClick, className, disabled, ...props }) {
  return <PrimaryButton disabled={disabled} className={className} onClick={onClick} {...props} />;
}

index.propTypes = {
  className: PropTypes.string,
  onClick: PropTypes.func,
};
